import { KeyValuePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth, VhImage, VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-design-menu',
  templateUrl: './design-menu.component.html',
  styleUrls: ['./design-menu.component.scss']
})
export class DesignMenuComponent {
  listTab: Array<any> = [{ _id: 0, name: "Restaurant" }, { _id: 1, name: "Cafe" }, { _id: 2, name: "Saved menu" }]

  savedMenus: Array<any> = []
  constructor(
    private cdRef: ChangeDetectorRef,
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAuth: VhAuth,
    private vhImage: VhImage,
    private keyValPipe: KeyValuePipe
  ) { }

  myTrackByFunction(element, index) {
    return element._id ? element._id : index
  }

  ngAfterViewChecked() {
    this.cdRef.detectChanges()
  }

  indexTab: number = 0;
  areaChanged(id) {
    if (id == 2) {
      this.getSavedMenus();
    }
    this.indexTab = id
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/design']);
  }

  /**Modal add */
  visibleSelectMenu: boolean = false;
  handleCancelSelectMenu() {
    this.visibleSelectMenu = false;
  }

  select(url, dataSave?: any) {
    this.router.navigate(['/cafe/dashboard/management/design/design-menu/template', url], { state: dataSave })
  }
  /** Lấy bản save menu */
  getSavedMenus() {
    this.vhQueryCafe.getPatternMenus().then((menu: any[]) => {
      this.savedMenus = menu.map(item => {
        return { ...item, editor: this.vhAuth.getlocalEmployee(item.id_editor).name, loading: false }
      })
    })
  }
  /**Xóa menu
   * @param menu: menu muốn xóa
   */
  async deleteMenu(menu: any) {
    // Tạo loading
    menu.loading = true
    let images: string[] = []
    // Xóa image của page
    if (menu.img) {
      for (let page of menu.img) {
        for (let image of this.keyValPipe.transform(page)) {
          if (image.value['src'].includes("http"))
            images.push(image.value['src'])
        }
      }
    }
    // Xóa image trang cover
    if (menu.title) {
      if (menu.title.img)
        if (Array.isArray(menu.title.img)) {
          for (let image of menu.title.img) {
            if (image.src.includes("http"))
              images.push(image.src)
          }
        } else if (menu.title.img.src.includes("http")) images.push(menu.title.img.src)
    }
    // xóa menu
    if (images.length == 0) this.vhQueryCafe.deletePatternMenu(menu._id)
    else
      await Promise.all([
        this.vhImage.deleteThumbnail(images),
        this.vhQueryCafe.deletePatternMenu(menu._id)
      ])

    this.savedMenus.splice(this.savedMenus.findIndex(item => item._id == menu._id), 1)
  }
}
