import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DesignMenuComponent } from './design-menu.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { KeyValuePipe } from '@angular/common';
const routes: Routes = [
  {
    path: "",
    component: DesignMenuComponent
  },
  {
    path: "template",
    loadChildren: () => import('./menu-template/menu-template.module').then(m => m.MenuTemplateModule)
  }
]

@NgModule({
  declarations: [DesignMenuComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule.withConfig({ warnOnNgModelWithFormControl: 'never' }),
  ],
  providers: [KeyValuePipe]
})
export class DesignMenuModule { }
